export const is = (e, t) => {
  if (typeof is[t] === 'function') {
    return is[t](e)
  } else {
    return typeof e === t
  }
}

// is.not = e => !is(e)

is.fn = e => typeof e === 'function'
is.str = e => typeof e === 'string'
is.obj = e => typeof e === 'object'
is.arr = e => Array.isArray(e)
is.bool = e => typeof e === 'boolean'

is.num = e => e === +e

is.int = e => e === +e && e === (e | 0)

