export const fragmentShader = `
//#extension GL_OES_standard_derivatives : enable

precision highp float;

uniform float uTime;
uniform vec2 uRes;
uniform vec3 dimensions;

float sphereSdf(vec3 point, float radius) {
	return distance(point, vec3(1.0, 1.0, 1.0)) - radius;
}

float boxSdf(vec3 point) {
	float xDiff = point.x - dimensions.x;
	float yDiff = point.y - dimensions.y;
	float zDiff = point.z - dimensions.z;
	/*
	float result = 0.0;
	if (abs(xDiff) <= abs(yDiff) && abs(xDiff) <= abs(zDiff)) {
		result = xDiff;
	} else if (abs(yDiff) <= abs(xDiff) && abs(yDiff) <= abs(zDiff)) {
		result = yDiff;
	} else {
		result = zDiff;
	}
*/
	return length(max(abs(point)-dimensions, 0.0));
}

float sdf(vec3 point) {
	vec3 translatedPoint = fract(point) - vec3(0.5, 0.5, 0.5);
	return boxSdf(translatedPoint);
}

void main( void ) {

	vec3 cameraPosition = vec3(0.0, 0.0, uTime);
	
	vec2 position = (2.0 * ( gl_FragCoord.xy / uRes.xy ) - 1.0);
	// position.y *= uRes.y / uRes.x;
	
	
	
	vec3 unitRay = normalize(vec3(position, 1.0));
	float rayMagnitude = 1.0;
	
	for (int i = 0; i < 100; i++) {
		float sd = sdf(unitRay / rayMagnitude - cameraPosition);
		rayMagnitude -= sd;
	}
	
	float brightness = 0.1 / (rayMagnitude * rayMagnitude);
	
	gl_FragColor = vec4(brightness, brightness, brightness, 1.0);

}
`
