import * as THREE from 'three'

import { mergeConfig } from '../../index.js'

import { vertexShader } from './vertexShader.js'
import { fragmentShader } from './fragmentShader.js'

export class TunnelMaterial extends THREE.ShaderMaterial {
  constructor(options) {
    const config = mergeConfig(options, {
      speed: 0.5,
      material: {
        transparent: true,
        side: THREE.DoubleSide,
        extensions: { derivatives: true },

        uniforms: {
          uTime: { value: 0, type: 'f' },
          uRes: { value: { x: 1400, y: 1400 }, type: 'i2' },
        },

        fragmentShader,
        vertexShader,
      },
    })

    super(config.material)

    this.config = config
  }

  render({ deltaTime }) {
    this.uniforms.uTime.value += deltaTime * this.config.speed
  }
}
