  /**
   * initiates the Main Scene Parts,
   * Lights, Scene, Camera, Clock etc
   *
   * @memberof ManifestEngine
   */
  initScene() {
    this.clock = new THREE.Clock()

    this.canvas = document.querySelector('canvas.webgl')

    // Scene
    this.scene = new THREE.Scene()

    this.ambientLight = new THREE.AmbientLight(0xa8b3c3, 1)
    this.scene.add(this.ambientLight)

    // this.ambientlight = new THREE.AmbientLight(0x85ffff, 1.5);
    // this.scene.add (this.ambientlight)

    this.directionalLight = new THREE.DirectionalLight('#ffffff', 1.0)
    this.directionalLight.position.set(100, 200, -200)
    //this.directionalLight.target.position.set(17., 48.5, 189.7)
    this.directionalLight.castShadow = true
    this.directionalLight.shadow.camera.far = 600
    this.scene.add(this.directionalLight) // this.directionalLight.target)

    // const cameraHelper = new THREE.CameraHelper(this.directionalLight.shadow.camera)
    // this.scene.add(cameraHelper);

    const { innerHeight: height, innerWidth: width } = window

    // Base camera
    this.camera = new THREE.PerspectiveCamera(60, width / height, 0.1, 6000)
    this.camera.rotation.x = THREE.MathUtils.degToRad(-90)

    //Renderer
    /** @type {THREE.WebGLRenderer} */
    this.renderer = new THREE.WebGLRenderer({
      canvas: this.canvas,
      alpha: true,
    })

    this.renderer.setClearAlpha(0)
    this.renderer.outputColorSpace = THREE.LinearSRGBColorSpace
    this.renderer.setSize(width, height)
    this.renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

    // this.renderer.shadowMap.enabled = true
    // this.renderer.shadowMap.type = THREE.PCFSoftShadowMap

    this.scene.fog = new THREE.FogExp2(0xa8b3c3, 0.0)

    this.compositShaderMaterial = new CompositShaderMaterial({ width, height })

    this.addAudio()

    window.addEventListener('resize', this.onResize.bind(this))
  }

  addAudio() {
    const audioElement = document.getElementsByTagName('audio')[0]

    const playAudio = () => {
      try {
        if (audioElement.paused) {
          audioElement.play()
          if (!audioElement.paused) {
            window.removeEventListener('mousedown', playAudio)
            // window.removeEventListener('keydown', playAudio)
          }
        }
      } catch (e) {}
    }

    window.addEventListener('mousedown', playAudio)
    // window.addEventListener('keydown', playAudio)
  }

  /**
   * Creates particleCount Particles
   *
   * @memberof ManifestEngine
   */
  particles() {
    //const objectDistance = 4

    //particles

    const positions = new Float32Array(this.config.particleCount * 3)

    for (let i = 0; i < this.config.particleCount; i++) {
      positions[i * 3 + 0] = (Math.random() - 0.5) * 1000
      positions[i * 3 + 1] = (Math.random() - 0.5) * 2000
      //positions[i * 3 + 1] = objectDistance * 0.5 - Math.random() * objectDistance * 10
      positions[i * 3 + 2] = (Math.random() - 0.5) * 1000
    }

    const particlesGeometry = new THREE.BufferGeometry()
    particlesGeometry.setAttribute('position', new THREE.BufferAttribute(positions, 3))

    const particlesMaterial = new THREE.PointsMaterial({
      color: '#fafafa',
      alphaMap: this.particleTexture,
      transparent: true,
      sizeAttenuation: true,
      alphaTest: 0.001,
      size: 40,
    })

    const particles = new THREE.Points(particlesGeometry, particlesMaterial)
    const { x, y, z } = this.moon.position
    particles.position.set(x, y, z)
    this.mainScene.add(particles)
    this.particles = particles
  }

  createCompositComposer() {
    const composer = new EffectComposer(this.renderer)

    const compositPass = new ShaderPass(this.compositShaderMaterial)
    composer.addPass(compositPass)

    return composer
  }

  createMainComposer(targetTexture) {
    const composer = new EffectComposer(this.renderer, targetTexture)

    const renderPass = new RenderPass(this.scene, this.camera)
    renderPass.clear = true
    renderPass.alpha = true
    renderPass.transparent = true
    renderPass.premultipliedAlpha = true
    renderPass.clearColor = new THREE.Color(0, 0, 0)
    renderPass.clearAlpha = 0

    composer.addPass(renderPass)

    composer.addPass(new ShaderPass(GammaCorrectionShader))

    const params = {
      shape: 1,
      radius: 4,
      rotateR: Math.PI / 12,
      rotateB: (Math.PI / 12) * 2,
      rotateG: (Math.PI / 12) * 3,
      scatter: 0,
      blending: 1,
      blendingMode: 1,
      greyscale: false,
      disable: false,
    }

    const halftonePass = new HalftonePass(
      new THREE.Vector2(window.innerWidth, window.innerHeight),
      params,
    )
    //halftonePass.transparent = true
    //dotPass.material.uniforms.blendingMode = 4
    halftonePass.material.uniforms.radius = { value: 3.5 }
    halftonePass.material.uniforms.blending = { value: 0.9 }
    halftonePass.material.uniforms.blendingMode = { value: 3 }
    //halftonePass.material.uniforms.scatter= {value: 0.1}

    halftonePass.material.uniforms.shape = { value: 4 }

    halftonePass.enabled = false

    this.halftonePass = halftonePass
    composer.addPass(halftonePass)

    const bloomPass = new UnrealBloomPass(
      new THREE.Vector2(window.innerWidth, window.innerHeight),
      0.6,
      0.2,
      0.01,
    )
    this.bloomPass = bloomPass
    composer.addPass(bloomPass)

    // const effect3 = new ShaderPass(HorizontalTiltShiftShader)
    // effect3.enabled = false
    // composer.addPass(effect3)

    // const bloomPass = new BloomPass(1.0, 40.0,1.0)
    // this.mainBloomPass = bloomPass

    const rgbShiftPass = new ShaderPass(RGBShiftShader)
    rgbShiftPass.material.uniforms.amount.value = 0.005
    rgbShiftPass.uniforms.blending = { value: 0.0 }

    // effect4.enabled = false
    composer.addPass(rgbShiftPass)
    this.rgbPass = rgbShiftPass

    /*
     * we always need a copypass.
     * this allows us to disable the other shaderpasses without flickering.
     */
    const copyPass = new ShaderPass(CopyShader)
    composer.addPass(copyPass)

    /*
     * we always need an outputpass.
     * this prevents flickering and/or invisibility issues.
     */
    const outputPass = new OutputPass()
    composer.addPass(outputPass)

    return composer
  }

