// do not import from '../../lib/index.js', it includes W which uses window

import { is } from './is.js'

export const mergeConfig = (customConfig = {}, defaultConfig = {}) => {
  if (!customConfig) {
    return defaultConfig
  }

  if (is.arr(customConfig) && is.arr(defaultConfig)) {
    if (customConfig.length) {
      return customConfig
    }

    return defaultConfig
  }

  return Object.fromEntries(
    Object.entries(defaultConfig).map(([key, val]) => {
      if (is.arr(val)) {
        if (is.arr(customConfig[key])) {
          return [key, customConfig[key]]
        }

        return [key, val]
      }
      if (is.obj(val) || is.obj(customConfig[key])) {
        return [key, mergeConfig(customConfig[key], val)]
      }

      return [key, customConfig.hasOwnProperty(key) ? customConfig[key] : val]
    }),
  )
}
