import { Mesh, SphereGeometry, MeshBasicMaterial, MeshStandardMaterial } from 'three'

/**
 * converts latitude and longitude to a Vector3 position on a sphere.
 *
 * @param {number} lat
 * @param {number} lng
 * @returns
 */
const convertCartesian = (lat, lng) => {
  let phi = ((90 - lat) * Math.PI) / 180
  let theta = ((lng + 180) * Math.PI) / 180

  let x = -Math.sin(phi) * Math.cos(theta)
  let y = Math.cos(phi)
  let z = Math.sin(phi) * Math.sin(theta)

  return {
    x,
    y,
    z,
  }
}
/**
 *
 *
 * @export
 * @class WorldDotMesh
 * @extends {Mesh}
 */
export class WorldDotMesh extends Mesh {
  /**
   * Creates an instance of WorldDotMesh.
   * @param {{ lat: number, lng: number }} point
   *
   * @param {object} options
   * @param {number} options.color
   * @param {number} options.width
   * @param {number} options.height
   * @param {number} options.widthSegments
   * @param {number} options.heightSegments
   * @param {number} options.radius
   * @memberof WorldDotMesh
   */
  constructor(point, options = {}) {
    const { lat, lng } = point

    const {
      color = 0xffffff,
      size = 1,
      widthSegments = 4,
      heightSegments = 4,
      radius = 1,
    } = options

    const geometry = new SphereGeometry(size, widthSegments, heightSegments)
    const material = new MeshStandardMaterial({ color, emissiveIntensity: 100 })

    super(geometry, material)

    const { x, y, z } = convertCartesian(lat, lng)

    const radiusWithPadding = radius + 1

    this.position.set(x * radiusWithPadding, y * radiusWithPadding, z * radiusWithPadding)
    // console.log('set worlddotmesh position', this.position)
  }
}
