const lastLogTimes = {}

export const logRegularily = (value, name = 'default', delay = 1000) => {
  const now = new Date().getTime()

  if (!lastLogTimes[name]) {
    lastLogTimes[name] = now
  }

  if (lastLogTimes[name] + delay < now) {
    lastLogTimes[name] = now
    console.log(name + ':', value)
  }
}
