      animNames.forEach(animName => {
        const action = engine.actions.find(a => a._clip.name === animName)
        const duration = action._clip.duration
        const mixer = engine.customMixers[animName]
        const mixerTime = currentPercent * duration

        mixer.setTime(mixerTime)
      })

      //logRegularily({ mixerTime, duration }, 'mixerTime')
