export const events = {
  before: scrollEvents.filter(e => e.before).sort((a, b) => (a.before < b.before ? 1 : -1)),
  after: scrollEvents.filter(e => e.after).sort((a, b) => (a.after > b.after ? 1 : -1)),
  animation: scrollEvents.filter(e => e.start && e.end),
}
