import { LinearFilter, RGBAFormat, ShaderMaterial, WebGLRenderTarget } from 'three'

/*
 * CompositShaderMaterial
 *
 * blends multiple renderTargets into one texture for rendering.
 *
 * const height = window.innerHeight
 * const width = window.innerWidth
 * const compositShader = new CompositShaderMaterial({ height, width })
 * const composer = new EffectComposer(renderer);
 * const compositPass = new ShaderPass(compositShader);
 * composer.addPass(compositPass);
 */

export class CompositShaderMaterial extends ShaderMaterial {
  constructor({ height, width }) {
    const parameters = {
      minFilter: LinearFilter,
      magFilter: LinearFilter,
      format: RGBAFormat,
      // stencilBuffer: true,
    }

    const target1 = new WebGLRenderTarget(width, height, parameters)
    const target2 = new WebGLRenderTarget(width, height, parameters)
    const target3 = new WebGLRenderTarget(width, height, parameters)

    const uniforms = {
      input1: { value: target1.texture },
      input2: { value: target2.texture },
      input3: { value: target3.texture },
      time: { value: 0.0, type: 'f' },
    }

    super({
      uniforms,
      vertexShader,
      fragmentShader,
    })

    this.target1 = target1
    this.target2 = target2
    this.target3 = target3
  }

  disposeTargets() {
    this.target1.dispose()
    this.target2.dispose()
    this.target3.dispose()
  }
}

const vertexShader = `
varying vec2 vUv;
void main()	{
  vUv = uv;
  gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 1.0 );
}
`

const fragmentShader = `
precision highp float;

varying vec2 vUv;

uniform sampler2D input1;
uniform sampler2D input2;
uniform sampler2D input3;

void main()	{
  vec4 texel1 = texture2D(input1, vUv);
  vec4 texel2 = texture2D(input2, vUv);
  vec4 texel3 = texture2D(input3, vUv);

  float overlapFactor = (1.0 - texel1.a) * texel2.a;
  vec4 overlappedTexel = texel1 + texel2 * overlapFactor;

  // gl_FragColor = overlappedTexel;

  float overlapFactor2 = (1.0 - overlappedTexel.a) * texel3.a;
  vec4 finalTexel = overlappedTexel + texel3 * overlapFactor2;
  gl_FragColor = finalTexel;
}
`
