import * as THREE from 'three'

import { mergeConfig } from '../../index.js'

import { vertexShader } from './vertexShader.js'
import { fragmentShader } from './fragmentShader.js'

export class CubeMaterial extends THREE.ShaderMaterial {
  constructor(options) {
    const config = mergeConfig(options, {
      speed: 0.5,
      material: {
        transparent: true,
        side: THREE.DoubleSide,
        extensions: { derivatives: true },

        uniforms: {
          uTime: { value: 0, type: 'f' },
          uRes: { value: { x: window.innerWidth, y: window.innerHeight }, type: 'i2' },
          dimensions: { value: new THREE.Vector3(0.1, 0.1, 0.1) },
        },

        fragmentShader,
        vertexShader,
      },
    })

    super(config.material)

    this.config = config
  }

  render({ deltaTime, scrollPercent }) {
    this.uniforms.uTime.value = scrollPercent * this.config.speed
  }
}
